.formControl.error input,
.formControl.error textarea{
    border: 1px red solid;
}

.formControl.error span{
   color: red;
}

.formSummaryError{
    border: #ff3300 1px solid;
    padding: 5px;
    color: #ff3300;
}
.password {
    position: relative;
}
.password-control {
    position: absolute;
    top: 11px;
    right: 6px;
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(https://cdn2.iconfinder.com/data/icons/picol-vector/32/view-512.png) 0 0 no-repeat;
}
.password-control.view {
    background: url(data:image/jpeg;base64,) 0 0 no-repeat;
}